import DoneIcon from '@mui/icons-material/Done';
import ErrorIcon from '@mui/icons-material/Error';
import { CircularProgress } from '@mui/material';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Stepper from '@mui/material/Stepper';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { AdapterMoment } from '@mui/x-date-pickers/AdapterMoment';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { Moment } from 'moment';
import 'moment/locale/de';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import ImageUploadZone from '../ImageUploadZone/ImageUploadZone';
import { postDataSecure, putDataSecure } from '../services/RestHandler';
import { RootState } from '../store/Store';

interface Image extends File {
    src: string;
}

interface Manual extends File {
}

interface SetPreviewImage {
    name: string;
}

export default function ItemAddPage() {
    const convertDateToString = (date: Moment | undefined) => {
        if (date === undefined) {
            return "";
        }
        const year = date.year();
        const month = String(date.month()).padStart(2, '0');
        const day = String(date.date()).padStart(2, '0');

        return `${year}-${month}-${day}`;
    }

    const [activeStep, setActiveStep] = React.useState(0);
    const [itemName, setItemName] = useState('');
    const [itemDescription, setItemDescription] = useState('');
    const [itemQuantity, setItemQuantity] = useState(0);
    const [itemUnitPrice, setItemUnitPrice] = useState(0.0);
    const [itemPhoto, setItemPhoto] = useState('');
    const [buyPlace, setBuyPlace] = useState("");
    const [buyDate, setBuyDate] = useState<Moment>();


    const [uploadingState, setUploadingState] = useState("not_uploaded");

    const [images, setImages] = useState<Image[]>([]);
    const [favoriteImage, setFavoriteImage] = useState("");

    const [manuals, setManuals] = useState<Manual[]>([]);

    const [invoiceFile, setInvoiceFile] = useState<File>();
    const [invoiceFileName, setInvoiceFileName] = useState('');

    const navigate = useNavigate();
    const dispatch = useDispatch();

    const userToken = useSelector<RootState, string>(
        (state) => state.user.user.token
    );

    const [steps, setSteps] = useState(['Artikelinformationen', 'Kaufinformationen', 'Fotos', 'Fertigstellung']);

    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth < 550) {
                const newSteps = ['', '', '', ''];
                setSteps(newSteps);
            } else {
                const newSteps = ['Artikelinformationen', 'Kaufinformationen', 'Fotos', 'Fertigstellung'];
                setSteps(newSteps);
            }
        };

        handleResize()

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const saveNewItem = async () => {
        setUploadingState("uploading");
        try {
            var response = await postDataSecure("/items", {
                name: itemName,
                description: itemDescription,
                purchaseInfo: {
                    date: convertDateToString(buyDate),
                    place: buyPlace,
                    quantity: itemQuantity,
                    unitPrice: itemUnitPrice
                }
            }, userToken);

            if (images.length !== 0) {
                var uploadedImages = await addImages(response.id);

                if (favoriteImage.length !== 0) {
                    var favoriteImageID = ""
                    for (const file of uploadedImages) {
                        if (file.fileName === favoriteImage) {
                            favoriteImageID = file.id;
                            break;
                        }
                    }

                    if (favoriteImageID.length === 0) {
                        throw new Error("File not found");
                    }

                    await setPreviewImage(response.id, { name: favoriteImageID });
                }
            }

            if (invoiceFileName.length !== 0) {
                await uploadInvoice(response.id);
            }

            setUploadingState("success");
        } catch (error) {
            setUploadingState("error");
        }
    };

    const handleNext = () => {
        if (activeStep === steps.length - 2) {
            saveNewItem()
        } if (activeStep === steps.length - 1) {
            navigate("/frontend/home");
        } else {
            setActiveStep((prevActiveStep) => prevActiveStep + 1);
        }
    };

    const addImages = async (itemID: string) => {
        const formData = new FormData();
        images.forEach((image) => {
            formData.append('images', image);
        });

        try {
            var response = await postDataSecure('/items/' + itemID + '/images', formData, userToken);
            return response.uploadedImages
        } catch (error) {
            console.error('Fehler beim Hochladen der Bilder:', error);
        }
    };

    const uploadInvoice = async (itemID: string) => {
        const formData: any = new FormData();
        formData.append('invoice', invoiceFile);

        try {
            await postDataSecure('/items/' + itemID + '/invoice', formData, userToken);
        } catch (error) {
            console.error('Fehler beim Hochladen der Anleitungen:', error);
        }
    };

    const setPreviewImage = async (itemID: string, preview: SetPreviewImage) => {
        try {
            await putDataSecure('/items/' + itemID + '/images/preview', preview, userToken);
        } catch (error) {
            console.error('Fehler beim Hochladen des Preview Images:', error);
        }
    };

    const stepValid = () => {
        if (activeStep === 0) {
            return itemName.length !== 0;
        } else if (activeStep === 1) {
            return true;
        } else if (activeStep === 2) {
            return true;
        } else if (activeStep === 3) {
            return true;
        } else {
            return false;
        }
    };

    const handleDateChange = (date: Moment | null) => {
        if (date === null) {
            return;
        }
        setBuyDate(date);
    };

    const handleInvoiceUpload = (event: any) => {
        const file = event.target.files[0];
        setInvoiceFile(file);
        setInvoiceFileName(file.name);
    };

    return (
        <Box sx={{ margin: 'auto', mt: 5 }} maxWidth="60em">
            <Stepper activeStep={activeStep} >
                {steps.map((label, index) => {
                    const stepProps: { completed?: boolean } = {};
                    const labelProps: {
                        optional?: React.ReactNode;
                    } = {};
                    return (
                        <Step key={index} {...stepProps}>
                            <StepLabel {...labelProps}>{label}</StepLabel>
                        </Step>
                    );
                })}
            </Stepper>
            <React.Fragment>
                <Box sx={{ mt: 5, mr: 2, ml: 2 }}>
                    {activeStep === 0 ? (
                        <>
                            <Typography sx={{ textAlign: 'center', fontWeight: 'bold' }}>Geben Sie den Namen des Artikels ein</Typography>
                            <br />
                            <TextField fullWidth value={itemName} onChange={(event) => setItemName(event.target.value)} id="itemname" label="Name" variant="standard" />
                            <br />
                            <br />
                            <TextField fullWidth multiline rows={4} value={itemDescription} onChange={(event) => setItemDescription(event.target.value)} label="Beschreibung" variant="standard" />
                        </>
                    ) : (
                        <>
                            {activeStep === 1 ? (
                                <>
                                    <Typography sx={{ textAlign: 'center', fontWeight: 'bold' }}>Geben Sie Kaufinformationen ein</Typography>
                                    <br></br>
                                    <Box display="flex" justifyContent="space-between">
                                        <TextField fullWidth sx={{ maxWidth: "48%" }} value={buyPlace} onChange={(event) => setBuyPlace(event.target.value)} label="Kaufort" variant="standard" />
                                        <LocalizationProvider dateAdapter={AdapterMoment} adapterLocale="de">
                                            <DatePicker sx={{ width: "48%" }} value={buyDate} onChange={handleDateChange} label="Kaufdatum" />
                                        </LocalizationProvider>
                                    </Box>

                                    <br />
                                    <br />
                                    <Box display="flex" justifyContent="space-between">
                                        <TextField fullWidth sx={{ maxWidth: "48%" }} value={itemQuantity} onChange={(event) => setItemQuantity(Number(event.target.value))} type="number" label="Anzahl" variant="standard" />
                                        <TextField fullWidth sx={{ maxWidth: "48%" }} value={itemUnitPrice} onChange={(event) => setItemUnitPrice(Number(event.target.value))} type="number" inputProps={{ step: "0.01" }} label="Stckpreis" variant="standard" />
                                    </Box>
                                    <br></br>
                                    <br></br>
                                    <Box display="flex" justifyContent="space-between">
                                        <Button variant="contained" component="label">
                                            Rechnung hochladen
                                            <input accept="application/pdf" type="file" onChange={handleInvoiceUpload} hidden />
                                        </Button>
                                        <Typography>{invoiceFileName}</Typography>
                                    </Box>


                                </>
                            ) : (
                                <>
                                    {activeStep === 2 ? (
                                        <>
                                            <Typography sx={{ textAlign: 'center', fontWeight: 'bold' }}>Lade mindestens ein Foto hoch und whle ein Preview Bild aus</Typography>
                                            <ImageUploadZone images={images} setImages={setImages} favoriteImage={favoriteImage} setFavoriteImage={setFavoriteImage} />

                                        </>

                                    ) : (
                                        <>
                                            {activeStep === 3 ? (
                                                <>
                                                    {uploadingState === "uploading" ? (
                                                        <Box sx={{ display: 'flex' }}>
                                                            <CircularProgress size={"10em"} style={{ margin: "auto" }} />
                                                        </Box>
                                                    ) : (
                                                        <>
                                                            {
                                                                uploadingState === "success" ? (
                                                                    <>
                                                                        <Box sx={{ display: 'flex' }}>
                                                                            <DoneIcon color='success' style={{ fontSize: "10em", margin: "auto" }} />


                                                                        </Box>
                                                                        <br></br>
                                                                        <Box sx={{ display: 'flex' }}>
                                                                            <Typography style={{ margin: "auto", fontSize: "2em" }}>Der Gegenstand wurde erfolgreich gespeichert!</Typography>
                                                                        </Box>
                                                                    </>) : (
                                                                    <>
                                                                        <>
                                                                            {
                                                                                uploadingState === "error" ? (
                                                                                    <>
                                                                                        <Box sx={{ display: 'flex' }}>
                                                                                            <ErrorIcon color='error' style={{ fontSize: "10em", margin: "auto" }} />
                                                                                        </Box>
                                                                                        <br></br>
                                                                                        <Box sx={{ display: 'flex' }}>
                                                                                            <Typography style={{ margin: "auto", fontSize: "2em" }}>Beim Speichern des Gegenstands ist ein Fehler aufgetreten!</Typography>
                                                                                        </Box>
                                                                                    </>) : (
                                                                                    <>
                                                                                        <></>
                                                                                    </>)
                                                                            }
                                                                        </>
                                                                    </>)
                                                            }
                                                        </>)}
                                                </>
                                            ) : (
                                                < p > Error</p>
                                            )}
                                        </>
                                    )}
                                </>
                            )}
                        </>
                    )}

                    <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                        <Box sx={{ flex: '1 1 auto' }} />
                        <Button onClick={handleNext} disabled={!stepValid()}>
                            {activeStep === steps.length - 1 ? 'Zur bersicht' : 'Weiter'}
                        </Button>
                    </Box>
                </Box>
            </React.Fragment >
        </Box >
    );
}
