import { NavigateBefore, NavigateNext } from '@mui/icons-material';
import CloseIcon from '@mui/icons-material/Close';
import DeleteIcon from '@mui/icons-material/Delete';
import HideImageIcon from '@mui/icons-material/HideImage';
import { Button, Typography } from '@mui/material';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import IconButton from '@mui/material/IconButton';
import { saveAs } from 'file-saver';
import moment from 'moment';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import { DetailedItem, Item } from '../model/Item';
import { deleteDataSecure, fetchFileSecure, fetchFileSecurePDF, getDataSecure } from '../services/RestHandler';
import { RootState } from '../store/Store';

type ImageSrc = Map<string, any>;

export default function ItemViewPage() {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const { itemid } = useParams();

    const [item, setItem] = useState<DetailedItem>({
        id: "",
        name: "",
        description: "",
        purchaseInfo: {
            date: "",
            unitPrice: 0,
            quantity: 0,
            place: "",
            invoice: {
                id: "",
                fileName: "",
            }
        },
        images: {
            previewImage: "",
            images: []
        },
    });


    const [imageSrcs, setImageSrcs] = useState<ImageSrc>();
    const [currentImage, setCurrentImage] = useState("");

    const userToken = useSelector<RootState, string>(
        (state) => state.user.user.token
    );

    const items = useSelector<RootState, Item[]>(
        (state) => state.items.items
    );

    const handleClose = () => {
        navigate("/frontend/home");
    };

    const handleEdit = () => {
        navigate("/frontend/items/" + itemid + "/edit");
    };

    const deleteItemAsync = async () => {
        try {
            await deleteDataSecure("/items/" + itemid, userToken);
        } catch (error) {
            console.log("Failed to delete item");
        }
        navigate("/frontend/home");
    };

    const getItemAsync = async () => {
        try {
            var response = await getDataSecure("/items/" + itemid, userToken);
            setItem(response);
            setCurrentImage(response.images.previewImage);
        } catch (error) {
            console.log("Failed to save item");
        }
    };


    const loadImages = async () => {
        var imageSrcs: ImageSrc = new Map();

        if (item.id === undefined) {
            return;
        }

        for (const image of item.images.images) {
            const imageSrc = await fetchFileSecure("/items/" + item.id + "/images/" + image.id, userToken);
            imageSrcs.set(image.id, imageSrc);
        }
        setImageSrcs(imageSrcs)
    }

    useEffect(() => {
        loadImages();
    }, [item])

    useEffect(() => {
        getItemAsync();
    }, [])

    const getImageSrc = (imageName: string) => {
        if (imageSrcs === undefined) {
            return undefined;
        }
        return imageSrcs.get(imageName);
    }

    const previousImage = (currentImageKey: string) => {
        if (imageSrcs === undefined) {
            return undefined;
        }
        const keysArray = Array.from(imageSrcs.keys());
        const currentIndex = keysArray.indexOf(currentImageKey);

        if (currentIndex === -1) {
            return undefined;
        }

        const previousIndex = (currentIndex === 0) ? keysArray.length - 1 : currentIndex - 1;

        setCurrentImage(keysArray[previousIndex]);
    };

    const nextImage = (currentImageKey: string) => {
        if (imageSrcs === undefined) {
            return undefined;
        }
        const keysArray = Array.from(imageSrcs.keys());
        const currentIndex = keysArray.indexOf(currentImageKey);

        if (currentIndex === -1) {
            return undefined;
        }

        const nextIndex = (currentIndex === keysArray.length - 1) ? 0 : currentIndex + 1;
        setCurrentImage(keysArray[nextIndex]);
    };

    const formatDate = (date: string) => {
        const myMomentObject = moment(date, 'YYYY-MM-DD')
        if (myMomentObject.year() === 1) {
            return ""
        }
        return myMomentObject.format('DD.MM.YYYY')
    }

    const handleInvoiceDownload = async () => {
        try {
            const pdfSrc = await fetchFileSecurePDF("/items/" + item.id + "/invoice", userToken);

            const fileName = item.purchaseInfo.invoice.fileName;

            const blob = new Blob([pdfSrc]);

            saveAs(blob, fileName);
        } catch (error) {
            console.error('Fehler beim Herunterladen der PDF:', error);
        }
    };

    return (
        <Box>
            <IconButton aria-label="edit" size="large" onClick={handleClose} sx={{
                position: "fixed",
                top: (theme) => theme.spacing(10),
                right: (theme) => theme.spacing(2)
            }}>
                <CloseIcon fontSize="inherit" />
            </IconButton>
            <IconButton aria-label="edit" size="large" onClick={deleteItemAsync} sx={{
                position: "fixed",
                top: (theme) => theme.spacing(10),
                right: (theme) => theme.spacing(10)
            }}>
                <DeleteIcon fontSize="inherit" />
            </IconButton>
            {/*
            <IconButton aria-label="edit" size="large" onClick={handleEdit} sx={{
                position: "fixed",
                top: (theme) => theme.spacing(10),
                right: (theme) => theme.spacing(18)
            }}>
                <ModeEditIcon fontSize="inherit" />
            </IconButton>
            */}
            <Grid spacing={2} direction="column"
                alignItems="center"
                justifyContent="center">
                <Grid item xs={12}>
                    <h1 style={{ textAlign: 'center' }}>{item.name}</h1>
                </Grid>
                <Grid item xs={12} >
                    <Box display="flex" alignItems="center" justifyContent="center">
                        <IconButton onClick={() => previousImage(currentImage)}>
                            <NavigateBefore />
                        </IconButton>
                        <>
                            {currentImage !== "" ?
                                <Box
                                    component="img"
                                    sx={{
                                        height: 233,
                                        width: 350,
                                        maxHeight: { xs: 233, md: 167 },
                                        maxWidth: { xs: 350, md: 250 },
                                        objectFit: "contain",
                                    }}
                                    alt={item.name}
                                    src={getImageSrc(currentImage)}
                                >
                                </Box>
                                : <Box
                                    sx={{
                                        height: 233,
                                        width: 350,
                                    }}
                                    display="flex" alignItems="center" justifyContent="center"
                                >
                                    <HideImageIcon sx={{ fontSize: "10em" }} />
                                </Box>}
                        </>

                        <IconButton onClick={() => nextImage(currentImage)}>
                            <NavigateNext />
                        </IconButton>
                    </Box>
                </Grid>
                <Grid item xs={12}>
                    <Box display="flex" alignItems="center" justifyContent="center">
                        <Typography width="40%" whiteSpace={'pre-line'}>{item.description}</Typography>
                    </Box>
                    <br></br>
                    <Box display="flex" alignItems="center" justifyContent="center">
                        <Box width="40%">
                            <Typography whiteSpace={'pre-line'} textAlign={'start'}><b>Bestand:</b> {item.purchaseInfo.quantity}</Typography>
                            <Typography whiteSpace={'pre-line'}><b>Kaufort:</b> {item.purchaseInfo.place}</Typography>
                            <Typography whiteSpace={'pre-line'}><b>Stckpreis:</b> {item.purchaseInfo.unitPrice}</Typography>
                            <Typography whiteSpace={'pre-line'}><b>Kaufdatum:</b> {formatDate(item.purchaseInfo.date)}</Typography>
                            <br></br>
                            {item.purchaseInfo.invoice.id !== "" ? (
                                <Box display="flex" justifyContent="space-between">
                                    <Button onClick={handleInvoiceDownload} variant='contained'>Rechnung herunterladen ({item.purchaseInfo.invoice.fileName})</Button>
                                </Box>
                            ) : (<></>)}
                        </Box>
                    </Box>
                </Grid>
            </Grid>
        </Box>
    );
}
