import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Route, Routes, useNavigate } from "react-router-dom";
import Header from '../Header/Header';
import ItemAddPage from '../ItemAddPage/ItemAddPage';
import ItemEditPage from '../ItemEditPage/ItemEditPage';
import ItemListPage from '../ItemListPage/ItemListPage';
import ItemViewPage from '../ItemViewPage/ItemViewPage';
import LoginPage from '../LoginPage/LoginPage';
import MessageBox from '../MessageBox/MessageBox';
import { PrivateRoute } from '../PrivateRoute/PrivateRoute';
import RegistrationPage from '../RegistrationPage/RegistrationPage';
import { AppDispatch, RootState } from '../store/Store';
import { setMessage } from '../store/slices/message/MessageSlice';
import { loginUserTokenAsync } from '../store/slices/user/loginUserTokenAsync';
import { UserState, resetStatus } from '../store/slices/user/UserSlice';

export default function App() {
  const dispatch = useDispatch<AppDispatch>();
  const navigate = useNavigate();

  const userStateStatus = useSelector<RootState, string>(
    (state) => state.user.status
  );

  useEffect(() => {
    dispatch(loginUserTokenAsync())
  }, []);

  useEffect(() => {
    if (userStateStatus === "succeeded") {
      dispatch(resetStatus())
      navigate("/frontend/home");
    }

  }, [userStateStatus, navigate]);

  return (
    <>
      <Header></Header>
      <MessageBox></MessageBox>
      <Routes>
        <Route path="/frontend" element={<LoginPage />} />
        <Route path="/frontend/register" element={<RegistrationPage />} />
        <Route path="/frontend/home" element={<PrivateRoute><ItemListPage /></PrivateRoute>} />
        <Route path="/frontend/additem" element={<PrivateRoute><ItemAddPage /></PrivateRoute>} />
        <Route path="/frontend/items/:itemid" element={<PrivateRoute><ItemViewPage /></PrivateRoute>} />
        <Route path="/frontend/items/:itemid/edit" element={<PrivateRoute><ItemEditPage /></PrivateRoute>} />
      </Routes>
    </>

  );
}
