import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { AppDispatch, RootState } from '../store/Store';
import { setMessage } from '../store/slices/message/MessageSlice';
import { UserState } from '../store/slices/user/UserSlice';
import { RegisterRequestData, registerUserAsync } from "../store/slices/user/registerUserAsync";

export default function RegistrationPage() {
    const userState = useSelector<RootState, UserState>(
        (state) => state.user
    );

    const navigate = useNavigate();
    const dispatch = useDispatch<AppDispatch>();

    const [userFirstName, setUserFirstName] = useState('');
    const [userLastName, setUserLastName] = useState('');
    const [userEmail, setUserEmail] = useState('');
    const [userPassword, setUserPassword] = useState('');

    useEffect(() => {
        if (userState.user.token !== '') {
            navigate('/frontend/home');
        }

        if (userState.status === "failed") {
            dispatch(setMessage({ message: "Registration failed", severity: "error", timeout: 0}));
        }

        if (userState.status === "succeeded") {
            dispatch(setMessage({ message: "Registration was successful. Please login now.", severity: "success", timeout: 5}));
            navigate('/');
        }

    }, [userState, dispatch, navigate]);

    const handleLogin = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();

        const registerRequest: RegisterRequestData = {
            firstName: userFirstName,
            lastName: userLastName,
            email: userEmail,
            password: userPassword,
        };

        dispatch(
            registerUserAsync(registerRequest)
        );
    };

    const isValid = () => {
        return userFirstName !== '' && userLastName !== '' && userEmail !== '' && userPassword !== '';
    };

    return (
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <form onSubmit={handleLogin} style={{ width: '20em', paddingTop: '3em' }}>
                <TextField fullWidth value={userFirstName} onChange={(event) => setUserFirstName(event.target.value)} label="Vorname" variant="outlined" required />
                <br /><br />
                <TextField fullWidth value={userLastName} onChange={(event) => setUserLastName(event.target.value)} label="Nachname" variant="outlined" required />
                <br /><br />
                <TextField fullWidth value={userEmail} onChange={(event) => setUserEmail(event.target.value)} label="E-Mail" variant="outlined" required />
                <br /><br />
                <TextField fullWidth value={userPassword} onChange={(event) => setUserPassword(event.target.value)} variant="outlined" label="Passwort" type="password" required />
                <br /><br />
                <Button type="submit" variant="contained" disabled={!isValid() && userState.status !== "loading"}>Register</Button>
            </form>
        </div>
    );
}
