import { createAsyncThunk } from "@reduxjs/toolkit";
import { User } from "../../../model/User";
import { handleGetCookie } from "../../../services/CookieHandler";
import { getDataSecure } from '../../../services/RestHandler';

export interface RefreshTokenResponseData {
    token: string;
}

export interface MeResponseData {
    id: string;
    firstName: string;
    lastName: string;
    email: string;
}

export const loginUserTokenAsync = createAsyncThunk(
    'user/loginUserTokenAsync',
    async () => {
        const tokenCookie = handleGetCookie("token");
        if (tokenCookie === undefined) {
            const user: User = {
                id: "",
                firstName: "",
                lastName: "",
                email: "",
                token: "",
            };
            return user;
        }
        const me = await getDataSecure(`/users/me`, tokenCookie) as User;
        const user: User = {
            id: me.id,
            firstName: me.firstName,
            lastName: me.lastName,
            email: me.lastName,
            token: tokenCookie,
        };
        return user;
    }
);
