import DeleteIcon from '@mui/icons-material/Delete';
import FavoriteIcon from '@mui/icons-material/Favorite';

import { Card, CardContent, CardMedia, Grid, IconButton, Typography } from '@mui/material';
import React from 'react';
import { useDropzone } from 'react-dropzone';

interface Image extends File {
    src: string;
}

export default function ImageUploadZone({ images, setImages, favoriteImage, setFavoriteImage }: any) {
    const handleDrop = (acceptedFiles: File[]) => {
        const filesWithsrc = acceptedFiles.filter((file) => {
            const alreadyUploaded = images.some((uploadedFile: Image) => uploadedFile.name === file.name);
            return !alreadyUploaded;
        }).map((file) =>
            Object.assign(file, {
                src: URL.createObjectURL(file),
            })
        );

        setImages((prevFiles: Image[]) => {
            var newFiles = prevFiles.concat(filesWithsrc);

            if (newFiles.length > 0) {
                setFavoriteImage(newFiles[0].name);
            } else {
                setFavoriteImage("");
            }
            return newFiles;
        });


    };

    const { getRootProps, getInputProps, isDragActive } = useDropzone({
        onDrop: handleDrop,
        accept: {
            'image/png': ['.png', '.PNG'],
            'image/jpg': ['.jpg', '.JPG'],
            'image/jpeg': ['.jpeg', '.JPEG'],
        }
    });

    const handleSetFavorite = (event: React.MouseEvent, file: Image) => {
        event.stopPropagation();
        setFavoriteImage(file.name);
    };

    const handleDeleteFile = (event: React.MouseEvent, fileName: string) => {
        event.stopPropagation();

        setImages((prevFiles: Image[]) => {
            const updatedFiles = prevFiles.filter((file) => file.name !== fileName);
            if (favoriteImage === fileName) {
                if (updatedFiles.length > 0) {
                    setFavoriteImage(updatedFiles[0].name);
                } else {
                    setFavoriteImage("");
                }
            }
            return updatedFiles;
        });
    };

    const renderFilesrc = (file: Image) => (
        <Grid item key={file.name} columns={{ xs: 2, sm: 8, md: 10, lg: 12, xl: 12 }} justifyContent="center">
            <Card key={file.name} sx={{ width: "15em", height: "12em", padding: "0.5em" }}>
                <CardMedia
                    component="img"
                    sx={{ objectFit: "contain", height: "6em", margin: "auto" }}
                    src={file.src}
                    alt={file.name}
                />
                <CardContent>
                    <Typography gutterBottom variant="body2" component="div">
                        {file.name}
                    </Typography>

                    <div
                        style={{
                            display: 'flex',
                            justifyContent: 'space-between',
                            alignItems: 'center',
                        }}
                    >
                        <IconButton
                            onClick={(event) => handleSetFavorite(event, file)}
                            style={{ marginLeft: '0' }}
                        >
                            <FavoriteIcon sx={{ color: favoriteImage === file.name ? 'red' : undefined }} />
                        </IconButton>
                        <IconButton
                            onClick={(event) => handleDeleteFile(event, file.name)}
                            style={{ marginRight: '0' }}
                        >
                            <DeleteIcon />
                        </IconButton>
                    </div>
                </CardContent>
            </Card>
        </Grid>
    );

    return (
        <div {...getRootProps()} style={{ minHeight: '5em', border: '1px dashed gray', padding: '1em', display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
            <input {...getInputProps()} />
            {isDragActive ? (
                <Typography variant="body1">Lege die Dateien hier ab</Typography>
            ) : (
                <Typography variant="body1">Ziehe die Dateien hier rein oder klicke um Dateien hochzuladen</Typography>
            )}
            <Grid container spacing={2}>
                {images.map(renderFilesrc)}
            </Grid>
        </div>
    );
}
