import { createAsyncThunk } from "@reduxjs/toolkit";
import { User } from "../../../model/User";
import { postData } from '../../../services/RestHandler';


export interface MeResponseData {
    id: string;
    firstName: string;
    lastName: string;
    email: string;
}

export const meUserAsync = createAsyncThunk(
    'user/meUserAsync',
    async (_, { getState }) => {
        const state = getState() as any;
        const response = await postData(`/users/me`, state.user.user.token);
        return response as User;
    }
);
