import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import IconButton from '@mui/material/IconButton';
import TextField from '@mui/material/TextField';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import { Item } from '../model/Item';
import { RootState } from '../store/Store';
import { editItem } from '../store/slices/item/ItemSlice';

export default function ItemEditPage() {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const { itemid } = useParams();
    const items = useSelector<RootState, Item[]>(
        (state) => state.items.items
    );
    const display_item = items.find((item) => item.id === itemid);

    const [itemID, setItemID] = useState('');
    const [itemName, setItemName] = useState('');
    const [itemDescription, setItemDescription] = useState('');
    const [itemQuantity, setItemQuantity] = useState(0);
    const [itemImage, setItemImage] = useState('');

    useEffect(() => {
        if (display_item === undefined) {
            navigate("/frontend/home");
        } else {
            setItemID(display_item.id)
            setItemName(display_item.name)
            setItemDescription(display_item.description)
            setItemQuantity(display_item.quantity)
            setItemImage(display_item.previewImage)
        }
    }, [display_item, navigate]);

    const handleClick = () => {
        navigate("/frontend/home/");
    };

    const handleEditFinished = () => {
        dispatch(
            editItem({
                id: itemID,
                name: itemName,
                description: itemDescription,
                quantity: itemQuantity,
                previewImage: itemImage,
            })
        );
        navigate("/frontend/items/" + itemID);
    }

    return (
        <Box>
            <IconButton aria-label="edit" size="large" onClick={handleClick} sx={{
                position: "fixed",
                top: (theme) => theme.spacing(10),
                right: (theme) => theme.spacing(2)
            }}>
                <CloseIcon fontSize="inherit" />
            </IconButton>
            <IconButton aria-label="edit" size="large" onClick={handleEditFinished} sx={{
                position: "fixed",
                top: (theme) => theme.spacing(10),
                right: (theme) => theme.spacing(10)
            }}>
                <CheckIcon fontSize="inherit" />
            </IconButton>
            <Grid container spacing={2}
                justifyContent="center"
                alignItems="center"
                sx={{ margin: 'auto', mt: "5em" }}
                maxWidth="40em">
                <Grid item xs={10}>
                    <TextField fullWidth value={itemName} onChange={(event) => setItemName(event.target.value)} label="Name" variant="outlined" />
                </Grid>
                <Grid item xs={10}>
                    <TextField fullWidth value={itemImage} onChange={(event) => setItemImage(event.target.value)} label="Bild URL" variant="outlined" />
                </Grid>
                <Grid item xs={10}>
                    <TextField fullWidth multiline maxRows={4} value={itemDescription} onChange={(event) => setItemDescription(event.target.value)} label="Beschreibung" variant="outlined" />
                </Grid>
                <Grid item xs={10}>
                    <TextField fullWidth value={itemQuantity} onChange={(event) => setItemQuantity(Number(event.target.value))} variant="outlined" label="Bestand" type="number" />
                </Grid>
            </Grid>
        </Box>
    );
}
