import { Backdrop, Box, Button, CircularProgress, TextField } from '@mui/material';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { AppDispatch, RootState } from '../store/Store';
import { clearMessage, setMessage } from '../store/slices/message/MessageSlice';
import { UserState } from '../store/slices/user/UserSlice';
import { LoginRequestData, loginUserAsync } from "../store/slices/user/loginUserAsync";

export default function LoginPage() {
    const userState = useSelector<RootState, UserState>(
        (state) => state.user
    );

    const navigate = useNavigate();
    const dispatch = useDispatch<AppDispatch>();

    const [userEmail, setUserEmail] = useState('');
    const [userPassword, setUserPassword] = useState('');
    const [open, setOpen] = useState(false);

    useEffect(() => {
        if (userState.user.token !== '') {
            navigate('/frontend/home');
        }

        if (userState.status === "loading") {
            setOpen(true);
        } else {
            setOpen(false)
        }

        if (userState.status === "failed") {
            dispatch(setMessage({ message: "Wrong credentials", severity: "error", timeout: 0 }));
        }
    }, [userState, dispatch, navigate]);


    const handleLogin = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();

        const loginRequest: LoginRequestData = {
            email: userEmail,
            password: userPassword,
        };
        dispatch(clearMessage());

        dispatch(
            loginUserAsync(loginRequest)
        );
    };

    const isValid = () => {
        return userEmail !== '' && userPassword !== '';
    };

    return (
        <>
            {userState.user.token === '' ? (
                <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                    <form onSubmit={handleLogin} style={{ width: '20em', paddingTop: '3em' }}>
                        <TextField fullWidth value={userEmail} onChange={(event) => setUserEmail(event.target.value)} label="E-Mail" variant="outlined" required />
                        <br /><br />
                        <TextField fullWidth value={userPassword} onChange={(event) => setUserPassword(event.target.value)} variant="outlined" label="Passwort" type="password" required />
                        <br /><br />
                        <Box display="flex" justifyContent="space-between">
                            <Button type="submit" variant="contained" disabled={!isValid() || userState.status === "loading"}>Login</Button>
                            <Button variant="contained" disabled={userState.status === "loading"} onClick={() => navigate("/frontend/register")}>Register</Button>
                        </Box>
                    </form>
                    <Backdrop
                        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                        open={open}
                    >
                        <CircularProgress color="inherit" />
                    </Backdrop>
                </div>
            ) : (<></>)}
        </>

    );
}
