import AddIcon from '@mui/icons-material/Add';
import HelpIcon from '@mui/icons-material/Help';
import { CardActionArea, CardMedia, CircularProgress, Fab } from '@mui/material';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from "react-router-dom";
import { Item } from '../model/Item';
import { fetchFileSecure, getDataSecure } from '../services/RestHandler';
import { AppDispatch, RootState } from '../store/Store';
import { getItemsAsync } from "../store/slices/item/getItemsAsync";
import HideImageIcon from '@mui/icons-material/HideImage';
type ImageSrc = Map<string, any>;

export default function ItemListPage() {
  const navigate = useNavigate();
  const dispatch = useDispatch<AppDispatch>();
  const [items, setItems] = useState<Item[]>([]);
  const [imageSrcState, setImageSrcState] = useState<ImageSrc>();

  const token = useSelector<RootState, string>(
    (state) => state.user.user.token
  );

  useEffect(() => {
    dispatch(getItemsAsync());
  }, [dispatch]);

  const loadItems = async () => {
    var response = await getDataSecure("/items", token);
    setItems(response.items);
  };

  const loadImages = async () => {
    var images: ImageSrc = new Map();

    for (const item of items) {
      if (item.previewImage !== "") {
        const image = await fetchFileSecure("/items/" + item.id + "/images/" + item.previewImage, token);
        images.set(item.previewImage, image)
      }
    }
    setImageSrcState(images);
  };

  const loadSingleImage = async (imageName: string, itemID: string) => {
    return await fetchFileSecure("/items/" + itemID + "/images/" + imageName, token);
  };

  useEffect(() => {
    loadItems();
  }, []);

  useEffect(() => {
    loadImages();
  }, [items]);

  const getImageSrc = (imageName: string) => {
    if (imageSrcState === undefined) {
      return undefined;
    }
    return imageSrcState.get(imageName);
  }

  const openItem = (index: string) => {
    navigate("/frontend/items/" + index);
  };

  const handleClick = () => {
    navigate("/frontend/addItem");
  };

  return (
    <>
      <Box>
        <Fab color="secondary" aria-label="add" sx={{
          position: "fixed",
          bottom: (theme) => theme.spacing(2),
          right: (theme) => theme.spacing(2)
        }} onClick={handleClick}>
          <AddIcon />
        </Fab>
      </Box>
      <Grid container spacing={{ xs: 2, md: 3 }} columns={{ xs: 2, sm: 8, md: 10, lg: 12, xl: 12 }} justifyContent="center" sx={{ padding: "1em" }}>
        {items.map((value, index) => (
          <Grid item key={value.id}>
            <Card key={value.id} sx={{ width: "30em", height: "20em" }}>
              <CardActionArea onClick={() => { openItem(value.id) }} sx={{
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'flex-start',
              }}>
                {value.previewImage === undefined ?
                  (
                    <Box sx={{ padding: "1em 1em 0 1em", height: "13em" }}>
                      <CircularProgress size="10em" />
                    </Box>
                  ) : (
                    <>{value.previewImage.length === 0 ?
                      (<Box sx={{ padding: "1em 1em 0 1em", height: "13em" }}>
                        <HideImageIcon sx={{ fontSize: "10em" }} />
                      </Box>) : (
                        <CardMedia
                          component="img"
                          sx={{ padding: "1em 1em 0 1em", objectFit: "contain", height: "13em" }}
                          src={getImageSrc(value.previewImage)}
                          alt={value.name}
                        />)
                    }</>
                  )
                }

                <CardContent>
                  <Typography gutterBottom variant="h5" component="div">
                    {value.name + "  (" + value.quantity + ")"}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {value.description}
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        ))}
      </Grid>
    </>
  );
}
