import { createSlice } from "@reduxjs/toolkit";
import { User } from "../../../model/User";
import { loginUserAsync } from "./loginUserAsync";
import { registerUserAsync } from "./registerUserAsync";
import { meUserAsync } from "./meUserAsync";
import { refreshUserTokenAsync } from "./refreshUserTokenAsync";
import { handleSetCookie, removeCookie } from "../../../services/CookieHandler";
import { loginUserTokenAsync } from "./loginUserTokenAsync";

export interface UserState {
  user: User;
  status: 'idle' | 'loading' | 'succeeded' | 'failed';
  error: string | null;
}

const initialState = {
  user:
  {
    id: "",
    firstName: "",
    lastName: "",
    email: "",
    token: "",
  },
  status: 'idle',
  error: null,
} as UserState;

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    logoutUser(state) {
      state.user.id = "";
      state.user.firstName = "";
      state.user.lastName = "";
      state.user.email = ""
      state.user.token = "";
      removeCookie("token");
    },
    resetStatus(state) {
      state.status = 'idle';
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(loginUserAsync.pending, (state) => {
        state.status = 'loading';
        state.error = null;
      })
      .addCase(loginUserAsync.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.user = action.payload;
        handleSetCookie("token", action.payload.token);
      })
      .addCase(loginUserAsync.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.payload as string;
      })
      .addCase(registerUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(registerUserAsync.fulfilled, (state) => {
        state.status = 'succeeded';
      })
      .addCase(registerUserAsync.rejected, (state) => {
        state.status = 'failed';
      })
      .addCase(meUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(meUserAsync.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.user.email = action.payload.email;
        state.user.firstName = action.payload.firstName;
        state.user.lastName = action.payload.lastName;
        state.user.id = action.payload.id;
      })
      .addCase(meUserAsync.rejected, (state) => {
        state.status = 'failed';
      })
      .addCase(refreshUserTokenAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(refreshUserTokenAsync.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.user.email = action.payload.token;
      })
      .addCase(refreshUserTokenAsync.rejected, (state) => {
        state.status = 'failed';
      })
      .addCase(loginUserTokenAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(loginUserTokenAsync.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.user = action.payload;
      })
      .addCase(loginUserTokenAsync.rejected, (state) => {
        state.status = 'failed';
        state.user.id = "";
        state.user.firstName = "";
        state.user.lastName = "";
        state.user.email = ""
        state.user.token = "";
        removeCookie("token");
      })
      ;
  },
});

export const { logoutUser, resetStatus } = userSlice.actions;
export default userSlice.reducer;
