export const fetchFileSecure = async (path: string, token: string) => {
  const response = await axios.get(`${BASE_URL}` + path, {
    headers: {
      Authorization: token,
    },
    responseType: 'arraybuffer',
  });

  const base64 = btoa(
    new Uint8Array(response.data).reduce(
      (data, byte) => data + String.fromCharCode(byte),
      ''
    )
  );

  return `data:${response.headers['content-type']};base64,${base64}`;
};

export const fetchFileSecurePDF = async (path: string, token: string) => {
  const response = await axios.get(`${BASE_URL}` + path, {
    headers: {
      Authorization: token,
    },
    responseType: 'blob',
  });

  const base64 = btoa(
    new Uint8Array(response.data).reduce(
      (data, byte) => data + String.fromCharCode(byte),
      ''
    )
  );

  return response.data;
};
