import { createAsyncThunk } from "@reduxjs/toolkit";
import { postData } from '../../../services/RestHandler';


export interface RefreshTokenResponseData {
    token: string;
}

export const refreshUserTokenAsync = createAsyncThunk(
    'user/refreshUserTokenAsync',
    async (_, { getState }) => {
        const state = getState() as any;
        const response = await postData(`/users/refresh`, state.user.user.token);
        return response as RefreshTokenResponseData;
    }
);
