import type { PayloadAction } from "@reduxjs/toolkit";
import { createSlice } from "@reduxjs/toolkit";
import { Item } from "../../../model/Item";
import { getItemsAsync } from "./getItemsAsync";

export interface ItemsState {
  items: Item[];
  status: 'idle' | 'loading' | 'succeeded' | 'failed';
  error: string | null;
}

const initialState = {
  items: [],
  status: 'idle',
  error: null,
} as ItemsState;

const itemSlice = createSlice({
  name: "items",
  initialState,
  reducers: {
    storeItem(state, action: PayloadAction<Item>) {
      state.items.push(action.payload);
    },
    editItem(state, action: PayloadAction<Item>) {
      const foundItem = state.items.find(item => item.id === action.payload.id);
      if (foundItem !== undefined) {
        foundItem.name = action.payload.name;
        foundItem.quantity = action.payload.quantity;
        foundItem.description = action.payload.description;
        foundItem.previewImage = action.payload.previewImage;
      }
    },
    deleteItem(state, action: PayloadAction<String>) {
      const itemToDelete = state.items.findIndex(item => item.id === action.payload);
      state.items.splice(itemToDelete, 1)
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(getItemsAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(getItemsAsync.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.items = action.payload;
      })
      .addCase(getItemsAsync.rejected, (state) => {
        state.status = 'failed';
      })
      ;
  },
});

export const { storeItem, editItem, deleteItem } = itemSlice.actions;
export default itemSlice.reducer;
