import CloseIcon from '@mui/icons-material/Close';
import Alert from '@mui/material/Alert';
import Box from '@mui/material/Box';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import * as React from 'react';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Message } from '../model/Message';
import { AppDispatch, RootState } from '../store/Store';
import { clearMessage } from '../store/slices/message/MessageSlice';

export default function MessageBox() {

  type AlertType = "info" | "warning" | "error" | "success";

  const [open, setOpen] = React.useState(false);
  const [severity, setSeverity] = React.useState<AlertType>("info");

  const message = useSelector<RootState, Message>(
    (state) => state.message.message
  );

  const dispatch = useDispatch<AppDispatch>();

  useEffect(() => {
    if (message.severity !== "none") {
      setSeverity(message.severity);
      setOpen(true);
    } else {
      setOpen(false);
    }

    if (message.timeout !== 0) {
      setTimeout(() => {
        dispatch(clearMessage())
      }, message.timeout * 1000); // 10 Sekunden
    }


  }, [message, dispatch]);

  return (
    <Box sx={{ width: '100%' }}>
      <Collapse in={open}>
        <Alert
          severity={severity}
          action={
            <IconButton
              aria-label="close"
              color="inherit"
              size="small"
              onClick={() => {
                setOpen(false);
              }}
            >
              <CloseIcon fontSize="inherit" />
            </IconButton>
          }
          sx={{ mb: 2 }}
        >
          {message.message}
        </Alert>
      </Collapse>
    </Box>
  );
}
